export const PERIODIC_LIMITS_BOUNDS = 'periodic-limits-bounds';
export const TRANSACTIONAL_LIMITS_BOUNDS = 'transactional-limits-bound';

export { BoundedLimitItem as Limit } from '@backbase/data-ang/limits';

export const BOUNDTS_TYPE: { [key: string]: any } = {
  DAILY: 'daily',
  WEEKLY: 'weekly',
  MONTHLY: 'monthly',
  QUARTERLY: 'quarterly',
  YEARLY: 'yearly',
};

export interface LimitBound {
  period: string;
  custom: boolean;
  regular: {
    amount: string | number | undefined;
  };
  shadow: {
    amount: string | number | undefined;
  };
  regularConsumption?: number;
  shadowConsumption?: number;
}

export interface CustomLimitData {
  created: boolean;
  currency?: string;
  isHasShadow?: boolean;
  isUnlimited?: boolean;
  limitBounds: LimitBound[];
  uuid: {
    regular?: string;
    shadow?: string;
  };
  pending?: boolean;
}
