import {
  Component,
  Directive,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { FormControl } from '@angular/forms';

import { forkJoin, Subject, of, BehaviorSubject, Observable } from 'rxjs';
import { finalize, takeUntil, catchError, distinctUntilChanged } from 'rxjs/operators';

import { BbTemplate } from '@backbase/foundation-ang/core';
import { LimitKey } from '@backbase/data-ang/limits';

import { NotificationService } from '@backbase/ui-ang/notification';
import { AMOUNT_DELIMETER_REG_EXP } from '../constants/amount-delimeter-remover';
import { LimitsService } from './services/limits.service';
import { LimitBound, PERIODIC_LIMITS_BOUNDS, TRANSACTIONAL_LIMITS_BOUNDS } from './models/limits-bound-types.model';

export type ManageLimitsModalView = 'view' | 'edit';
const PENDING_APPROVAL_STATUS = 'PENDING';

@Component({
  selector: 'bb-manage-limits-modal',
  templateUrl: 'manage-limits-modal.component.html',
})
export class ManageLimitsModalComponent implements OnInit, OnDestroy {
  /**
   * Success both limits template reference
   */
  @ViewChild('successBothLimit')
  successBothLimitTemplate: TemplateRef<any> | string = '';
  /**
   * Success Regular limits create template reference
   */
  @ViewChild('successRegularLimitCreate')
  successRegularLimitCreateTemplate: TemplateRef<any> | string = '';
  /**
   * Success Regular limits update template reference
   */
  @ViewChild('successRegularLimitUpdate')
  successRegularLimitUpdateTemplate: TemplateRef<any> | string = '';
  /**
   * Success Shadow limits create template reference
   */
  @ViewChild('successShadowLimitCreate')
  successShadowLimitCreateTemplate: TemplateRef<any> | string = '';
  /**
   * Success shadow limit update template reference
   */
  @ViewChild('successShadowLimitUpdate')
  successShadowLimitUpdateTemplate: TemplateRef<any> | string = '';
  /**
   * Fail both limits template reference
   */
  @ViewChild('failBothLimit')
  failBothLimitTemplate: TemplateRef<any> | string = '';
  /**
   * Fail regular limit create template reference
   */
  @ViewChild('failRegularLimitCreate')
  failRegularLimitCreateTemplate: TemplateRef<any> | string = '';
  /**
   * Fail regular limit update template reference
   */
  @ViewChild('failRegularLimitUpdate')
  failRegularLimitUpdateTemplate: TemplateRef<any> | string = '';
  /**
   * Fail shadow limit create template reference
   */
  @ViewChild('failShadowLimitCreate')
  failShadowLimitCreateTemplate: TemplateRef<any> | string = '';
  /**
   * Fail shadow limit update template reference
   */
  @ViewChild('failShadowLimitUpdate')
  failShadowLimitUpdateTemplate: TemplateRef<any> | string = '';
  /**
   * Limits pending notification template reference
   */
  @ViewChild('limitsPendingNotificationTemplate')
  limitsPendingNotificationTemplate: TemplateRef<any> | string = '';
  /**
   * Shadow Limits pending notification template reference
   */
  @ViewChild('shadowLimitsPendingNotificationTemplate')
  shadowLimitsPendingNotificationTemplate: TemplateRef<any> | string = '';
  /**
   * Define which view template should be shown
   */
  @Input() view!: ManageLimitsModalView;
  /**
   * Limits data which passed to view containers.
   */
  @Input() limitBounds!: LimitBound[];
  /**
   * Flag for show/hide regular limit column.
   */
  @Input() showRegularLimit = false;
  /**
   * Flag for show/hide shadow limit column.
   */
  @Input() showShadowLimit = false;
  /**
   * Limit currency value
   */
  @Input() currency: string | undefined;
  /**
   * Limit key value
   */
  @Input() limitKey!: LimitKey;
  /**
   * Limit id's
   */
  @Input() limitIds?: { regular?: string; shadow?: string };
  /**
   * Pending flag
   */
  @Input() pending?: boolean;
  /**
   * Header title
   */
  @Input() headerTitle?: string;
  /**
   * Service agreement id
   */
  @Input() serviceAgreementId?: string;
  /**
   * Legal entity id
   */
  @Input() legalEntityId?: string;
  /**
   * User name for header subtitle
   */
  @Input() headerSubTitleUserName?: string;
  /**
   * Job role name for header subtitle
   */
  @Input() headerSubTitleJRName?: string;
  /**
   * Legal entity name for header subtitle
   */
  @Input() headerSubTitleLEName?: string;
  /**
   * Service agreement name for header subtitle
   */
  @Input() headerSubTitleSAName?: string;
  /**
   * Notification dismiss time in milliseconds (defaults to 3000)
   * @default 3000
   */
  @Input() notificationDismissTime = 3000;
  /**
   * Close modal window event.
   * Propagate event when modal window should be closed.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() close: EventEmitter<void> = new EventEmitter<void>();

  private readonly pendingSubmitSubject = new Subject<boolean>();
  /**
   * Stream of pending submit flag value
   */
  readonly pendingSubmit$: Observable<boolean> = this.pendingSubmitSubject.pipe(distinctUntilChanged());

  private readonly errorsBESubject = new Subject<any>();
  /**
   * Stream of BE errors
   */
  readonly errorsBE$: Observable<any> = this.errorsBESubject.pipe(distinctUntilChanged());

  private readonly disableRegularSubject = new BehaviorSubject<boolean>(false);
  /**
   * Stream of Disable regular limits flag
   */
  readonly disableRegular$: Observable<boolean> = this.disableRegularSubject.pipe(distinctUntilChanged());

  private readonly disableShadowSubject = new BehaviorSubject<boolean>(false);
  /**
   * Stream of Disable shadow limits flag
   */
  readonly disableShadow$: Observable<boolean> = this.disableShadowSubject.pipe(distinctUntilChanged());

  /**
   * Currency form control
   */
  readonly currencyControl: FormControl = new FormControl();

  /**
   * Show notification stream
   */
  private readonly showNotification$ = new Subject<{
    header: TemplateRef<any> | string;
    modifier: 'success' | 'error';
  }>();
  /**
   * Unsubscribe stream
   */
  private readonly destroy$ = new Subject();

  constructor(
    private readonly notificationService: NotificationService,
    private readonly limitsService: LimitsService,
  ) {}

  ngOnInit(): void {
    this.showNotification$.pipe(takeUntil(this.destroy$)).subscribe(({ header, modifier }) => {
      this.notificationService.showNotification({
        modifier,
        header,
        message: '',
        dismissible: true,
        ttl: this.notificationDismissTime,
      });
    });
  }

  /**
   * Function for calling create/update limits API calls
   * @param event - any
   */
  submitLimits(event: any): void {
    this.pendingSubmitSubject.next(true);
    this.errorsBESubject.next();

    let requestData;
    const requestDataList: any[] = [];
    const requests = [];

    if (this.showRegularLimit && !this.disableRegularSubject.value && event.isRegularChanged) {
      requestData = this.generateManageLimitRequestData(event.limitValue, false);
      requestDataList.push(requestData);
      requests.push(
        requestData.uuid
          ? this.limitsService
              .updateLimit(requestData.uuid, this.generateManageLimitRequestUpdateData(event.limitValue, false))
              .pipe(catchError(err => of({ isError: true, ...err })))
          : this.limitsService.createNewLimit(requestData).pipe(catchError(err => of({ isError: true, ...err }))),
      );
    }

    if (this.showShadowLimit && !this.disableShadowSubject.value && event.isShadowChanged) {
      requestData = this.generateManageLimitRequestData(event.limitValue, true);
      requestDataList.push(requestData);
      requests.push(
        requestData.uuid
          ? this.limitsService
              .updateLimit(requestData.uuid, this.generateManageLimitRequestUpdateData(event.limitValue, true))
              .pipe(catchError(err => of({ isError: true, ...err })))
          : this.limitsService.createNewLimit(requestData).pipe(catchError(err => of({ isError: true, ...err }))),
      );
    }

    forkJoin(requests)
      .pipe(
        finalize(() => this.pendingSubmitSubject.next(false)),
        takeUntil(this.destroy$),
      )
      .subscribe(resp => this.parseSubmitLimitsResponse(resp, requestDataList));
  }

  /**
   * Parse response of manage limits API
   * @param response - any
   * @param requests - any[]
   */
  parseSubmitLimitsResponse(response: any, requests: any[]): void {
    const errors = response.filter((item: any) => item.isError);

    if (!errors.length) {
      if (response.filter((item: any) => item.approvalStatus === PENDING_APPROVAL_STATUS).length) {
        this.showAllIsPendingNotification(requests);
      } else {
        this.showNotification(this.successBothLimitTemplate);
      }
      this.close.emit();
    } else if (errors.length === response.length) {
      this.showNotification(this.failBothLimitTemplate, 'error');
    } else {
      response.forEach((item: any, index: number) => {
        if (item.approvalStatus === PENDING_APPROVAL_STATUS) {
          this.showPendingLimitNotification(requests[index]);
        } else {
          this.showLimitNotification(requests[index], item.isError);
        }
      });
      if (!this.currency) {
        this.currency = this.currencyControl.value;
      }
    }

    this.generateBEErrors(errors);
  }

  /**
   * Generate list of BE errors
   * @param errors - any[]
   */
  generateBEErrors(errors: any[]): void {
    if (!errors.length) {
      return;
    }

    const errorsBE: any[] = [];
    errors.forEach((errorItem: any) => {
      const error = errorItem.error;
      if (error) {
        if (error.errorDetails) {
          errorsBE.push(...this.parseErrorDetails(error.errorDetails));
        } else if (error.message) {
          errorsBE.push({ message: error.message });
        }
      } else if (errorItem.message) {
        errorsBE.push({ message: errorItem.message });
      }
    });
    this.errorsBESubject.next(errorsBE);
  }

  /**
   * Parse errorDetails object and return array of error
   * @param errorDetails - any
   * @return any[]
   */
  parseErrorDetails(errorDetails: any): any[] {
    const errorsBE = [];
    if (errorDetails && errorDetails.errorFields) {
      errorsBE.push(...errorDetails.errorFields);
    } else if (errorDetails.message) {
      errorsBE.push({ message: errorDetails.message });
    }
    return errorsBE;
  }

  /**
   * Show appropriate notification after create/update limits
   * @param request - any
   * @param isError - boolean
   */
  private showLimitNotification(request: any, isError: boolean) {
    if (isError) {
      this.showErrorLimitNotification(request);
    } else {
      this.showSuccessLimitNotification(request);
    }
  }

  /**
   * Show error notification for limit request
   * @param request - any
   */
  private showErrorLimitNotification(request: any) {
    if (request.shadow) {
      this.showNotification(
        request.uuid ? this.failShadowLimitUpdateTemplate : this.failShadowLimitCreateTemplate,
        'error',
      );
    } else {
      this.showNotification(
        request.uuid ? this.failRegularLimitUpdateTemplate : this.failRegularLimitCreateTemplate,
        'error',
      );
    }
  }

  /**
   * Show success notification for limit request
   * @param request - any
   */
  private showSuccessLimitNotification(request: any) {
    if (!request.shadow) {
      this.showNotification(
        request.uuid ? this.successRegularLimitUpdateTemplate : this.successRegularLimitCreateTemplate,
      );
      this.disableRegularSubject.next(true);
    } else {
      this.showNotification(
        request.uuid ? this.successShadowLimitUpdateTemplate : this.successShadowLimitCreateTemplate,
      );
      this.disableShadowSubject.next(true);
    }
  }

  /**
   * Show success pending notification for limit requests
   * @param requests - any[]
   */
  private showAllIsPendingNotification(requests: any[]) {
    if (requests.length === 1 && requests[0].shadow) {
      this.showNotification(this.shadowLimitsPendingNotificationTemplate, 'success');
    } else {
      this.showNotification(this.limitsPendingNotificationTemplate, 'success');
    }
  }

  /**
   * Show pending notification which happened with another failed request
   * @param request - any
   */
  private showPendingLimitNotification(request: any) {
    if (request.shadow) {
      this.showNotification(this.shadowLimitsPendingNotificationTemplate, 'success');
      this.disableShadowSubject.next(true);
    } else {
      this.showNotification(this.limitsPendingNotificationTemplate, 'success');
      this.disableRegularSubject.next(true);
    }
  }

  /**
   * Show notification
   * @param messageRef - TemplateRef<any> | string
   * @param type - any
   */
  private showNotification(messageRef: TemplateRef<any> | string, type: any = 'success') {
    this.showNotification$.next({
      modifier: type,
      header: messageRef,
    });
  }

  /**
   * Mapper for converting limits form value into request body for manage limits API
   * @param limitValue - any
   * @param isShadow - boolean
   */
  private generateManageLimitRequestData(limitValue: any, isShadow: boolean) {
    const limitType = isShadow ? 'shadow' : 'regular';

    const requestData: any = {
      'user-BBID': this.limitKey['user-BBID'],
      entities: this.limitKey.lookupKeys,
      shadow: isShadow,
      currency: this.currency || this.currencyControl.value,
    };
    requestData[PERIODIC_LIMITS_BOUNDS] = {
      customPeriods: [],
    };

    if (this.limitIds && this.limitIds[limitType]) {
      requestData.uuid = this.limitIds[limitType];
    }

    this.limitBounds.forEach((limitBound: LimitBound, index: number) => {
      const amount = limitValue[limitType][index]?.amount.replace(AMOUNT_DELIMETER_REG_EXP, '');
      switch (limitBound.period) {
        case TRANSACTIONAL_LIMITS_BOUNDS:
          requestData[TRANSACTIONAL_LIMITS_BOUNDS] = {
            amount,
          };
          break;
        case 'daily':
        case 'weekly':
        case 'monthly':
        case 'quarterly':
        case 'yearly':
          requestData[PERIODIC_LIMITS_BOUNDS][limitBound.period] = amount;
          break;
        default:
          requestData[PERIODIC_LIMITS_BOUNDS].customPeriods.push({
            period: limitBound.period,
            amount,
          });
          break;
      }
    });

    return requestData;
  }

  private generateManageLimitRequestUpdateData(limitValue: any, isShadow: boolean) {
    const requestData: any = {};
    const limitType = isShadow ? 'shadow' : 'regular';
    requestData[PERIODIC_LIMITS_BOUNDS] = {
      customPeriods: [],
    };
    this.limitBounds.forEach((limitBound: LimitBound, index: number) => {
      const amount = limitValue[limitType][index]?.amount;
      switch (limitBound.period) {
        case TRANSACTIONAL_LIMITS_BOUNDS:
          requestData[TRANSACTIONAL_LIMITS_BOUNDS] = {
            amount,
          };
          break;
        case 'daily':
        case 'weekly':
        case 'monthly':
        case 'quarterly':
        case 'yearly':
          requestData[PERIODIC_LIMITS_BOUNDS][limitBound.period] = amount;
          break;
        default:
          requestData[PERIODIC_LIMITS_BOUNDS].customPeriods.push({
            period: limitBound.period,
            amount,
          });
          break;
      }
    });

    return requestData;
  }

  /**
   * Limit currency getter
   */
  get getCurrency(): any {
    return this.currency || this.currencyControl.value;
  }

  /**
   * OpenChange modal window event handler.
   * @param isOpen - boolean
   */
  onOpenChange(isOpen: boolean): void {
    if (!isOpen) {
      this.close.emit();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }
}

@Directive({
  selector: '[bbManageLimitsModalCustomizable]',
})
export class ManageLimitsModalCustomizableDirective extends BbTemplate<
  ManageLimitsModalComponent,
  {
    pendingSubmit: boolean | null;
    errorsBE: any[] | null;
    disableRegular: boolean | null;
    disableShadow: boolean | null;
  }
> {}
