import { HttpErrorResponse } from '@angular/common/http';
import { AbstractControl } from '@angular/forms';

export enum FormAction {
  CREATE,
  EDIT,
  LIST,
  ADD,
  REMOVE,
}

export interface WidgetError {
  kind: string;
  error: HttpErrorResponse;
  action: FormAction;
  keys: { message: string; key: string }[];
}

const errorKindMap: { [key: number]: string } = {
  0: 'ConnectivityError',
  400: 'BadRequest',
  401: 'AccessDenied',
  403: 'AccessDenied',
};

const extractKeys = (httpError: HttpErrorResponse) =>
  httpError && httpError.error && httpError.error.errors
    ? httpError.error.errors.map(({ message, key }: { message: string; key: string }) => ({
        message,
        key,
      }))
    : [];

export const parseError = (widgetName: string, error: HttpErrorResponse, action = FormAction.LIST): WidgetError => ({
  kind: `${widgetName}${errorKindMap[error.status] || 'UnknownError'}`,
  error,
  action,
  keys: extractKeys(error),
});

export const hasError = (control: AbstractControl | undefined | null, type: string): boolean => {
  if (!control || !control.errors) {
    return false;
  }
  return !!control.errors[type];
};

export const isFieldValid = (control: AbstractControl | undefined | null): boolean => {
  if (control) {
    return control.valid || control.untouched;
  }
  return false;
};

/**
 * Get original error msg for no typed error
 */
export const errorMsg = (error: WidgetError | undefined | null): string => {
  if (error && error.keys && error.keys.length) {
    const msgArray: Array<string> = [];
    error.keys.forEach((element: { message: any }) => msgArray.push(element.message));
    return msgArray.join('.');
  }
  return 'UnknownError';
};
