import { Injectable } from '@angular/core';
import { EMPTY, Observable } from 'rxjs';
import { ContactsGetResponseBodyItem, ContactsHttpService } from '@backbase/data-ang/contact-manager';
import { ProductSummaryHttpService } from '@backbase/data-ang/arrangements';
import { ServiceAgreementHttpService, ServiceAgreementsHttpService } from '@backbase/data-ang/accesscontrol';
import { catchError, map, switchMap } from 'rxjs/operators';
import { HttpResponse } from '@angular/common/http';
import { ServiceAgreement, ServiceAgreementParticipant, ServiceAgreementWithParticipants } from '../model/types';

interface GetPayeeGroupParams {
  query?: string;
  from?: number;
  size?: number;
  targetSaId?: string;
}

@Injectable()
export class UserPermissionsDataGroupDetailsService {
  /**
   * Service agreement from context stream
   */
  readonly serviceAgreementFromContext: Observable<ServiceAgreementWithParticipants> = this.serviceAgreementHttpService
    .getServiceAgreementContext()
    .pipe(switchMap((serviceAgreement: ServiceAgreement) => this.getServiceAgreementParticipants(serviceAgreement)));

  getServiceAgreementParticipants(serviceAgreement: ServiceAgreement): Observable<ServiceAgreementWithParticipants> {
    return this.serviceAgreementsHttpService.getServiceAgreementParticipants({ id: serviceAgreement.id }).pipe(
      map((participants: ServiceAgreementParticipant[]) => ({
        serviceAgreement,
        participants,
      })),
    );
  }
  constructor(
    private readonly contactService: ContactsHttpService,
    private readonly itemsService: ProductSummaryHttpService,
    private readonly serviceAgreementHttpService: ServiceAgreementHttpService,
    private readonly serviceAgreementsHttpService: ServiceAgreementsHttpService,
  ) {}

  /**
   * Send request to retrieve service agreement info
   * @param serviceAgreementId
   */
  getServiceAgreement(serviceAgreementId: string): Observable<ServiceAgreementWithParticipants> {
    return this.serviceAgreementHttpService.getServiceAgreementItem({ serviceAgreementId }).pipe(
      switchMap((serviceAgreement: ServiceAgreement) => this.getServiceAgreementParticipants(serviceAgreement)),
      catchError(() => EMPTY),
    );
  }

  getPayeeGroupContacts(contactIds: string[], params?: GetPayeeGroupParams): Observable<any> {
    return this.contactService
      .postContactsFilter(
        {
          ...params,
          contactsPostFilterRequestBody: {
            contactIds,
          },
        },
        'response',
      )
      .pipe(
        map((resp: HttpResponse<Array<ContactsGetResponseBodyItem>>) => {
          return {
            data: resp.body,
            totalCount: Number(resp.headers.get('x-total-count')),
          };
        }),
      );
  }

  getAccountGroupItems({ items, from, size, searchTerm, legalEntityIds }: any): Observable<any> {
    return this.itemsService.postFilterProductSummaries({
      productSummaryFilterParams: {
        legalEntityIds: legalEntityIds,
        arrangementIds: items,
        from,
        size,
        ...(searchTerm && { searchTerm }),
      },
    });
  }
}
