import { Injectable } from '@angular/core';
import { CanDeactivate } from '@angular/router';
import { Observable } from 'rxjs';
import { WithDiscardChangesComponent } from '../types';

@Injectable()
export class AccessgroupCanDeactivateGuardService implements CanDeactivate<WithDiscardChangesComponent> {
  canDeactivate(component: WithDiscardChangesComponent): Observable<boolean> {
    return component.openDiscardChangesModal();
  }
}
