import { FunctionsGetResponseBody } from '@backbase/data-ang/accesscontrol';
import {
  defaultBusinessFunctionGroups,
  BusinessFunctionToGroupConverter,
  defaultOrderedBusinessFunctionGroups,
} from '../helpers/business-function-group.constants';
import {
  BusinessFunction,
  BusinessFunctionGroup,
  BusinessFunctionBase,
  BusinessFunctionWithGroupKey,
} from '../model/types';

export class BusinessFunctionGroupService {
  constructor(
    private readonly customBusinessFunctionGroup: BusinessFunctionToGroupConverter,
    private readonly orderedBusinessFunctionGroups: string[],
  ) {}

  convertToGroups(allBusinessFunctions: BusinessFunction[]): BusinessFunctionGroup[] {
    const businessFunctionsByGroup: {
      [key: string]: BusinessFunction[];
    } = allBusinessFunctions.reduce((memo: { [key: string]: BusinessFunction[] }, item: BusinessFunction) => {
      const customGroupType = this.getGroupForBusinessFunction(item);

      return {
        ...memo,
        [customGroupType]: [...(memo[customGroupType] || []), item],
      };
    }, {});

    return Object.entries(businessFunctionsByGroup)
      .map(([groupKey, businessFunctions]: [string, BusinessFunction[]]) => ({
        groupKey,
        businessFunctions,
      }))
      .sort(
        (a: BusinessFunctionGroup, b: BusinessFunctionGroup) =>
          this.orderedBusinessFunctionGroups.indexOf(a.groupKey) -
          this.orderedBusinessFunctionGroups.indexOf(b.groupKey),
      );
  }

  getGroupForBusinessFunction(item: BusinessFunctionBase): string {
    const defaultGroupType =
      defaultOrderedBusinessFunctionGroups.find(groupType =>
        defaultBusinessFunctionGroups[groupType].includes(item.resource),
      ) || 'other';
    return this.customBusinessFunctionGroup(item, defaultGroupType);
  }

  attachGroupKeyToBusinessFunction(businessFunctions: FunctionsGetResponseBody[]): BusinessFunctionWithGroupKey[] {
    return businessFunctions
      .map(
        (bf: FunctionsGetResponseBody): BusinessFunctionWithGroupKey => ({
          ...bf,
          groupKey: this.getGroupForBusinessFunction(bf),
        }),
      )
      .sort(
        (a: BusinessFunctionWithGroupKey, b: BusinessFunctionWithGroupKey) =>
          this.orderedBusinessFunctionGroups.indexOf(a.groupKey) -
          this.orderedBusinessFunctionGroups.indexOf(b.groupKey),
      );
  }
}
