import { FunctionsGetResponseBody, Permission, PresentationPrivilege } from '@backbase/data-ang/accesscontrol';
import { BusinessFunction, BusinessFunctionPrivilege, FunctionGroup } from '../model/types';

const getBusinessFunctionPrivilege = (
  permission: Permission,
  businessFunction: FunctionsGetResponseBody,
  key: string,
): BusinessFunctionPrivilege => {
  const privilege = businessFunction.privileges.find((e: PresentationPrivilege) => e.privilege === key);
  const assigned = !!permission.assignedPrivileges.find(assignedPrivilege => assignedPrivilege.privilege === key);
  const supportsLimits = privilege ? privilege.supportsLimit : false;

  return {
    applicable: !!privilege,
    assigned,
    supportsLimits,
  };
};

export const PRIVILEGES = ['execute', 'view', 'create', 'edit', 'delete', 'approve', 'cancel'];

export const getExtendedBusinessFunctions = (
  functionGroup: FunctionGroup | undefined,
  allBusinessFunctions: FunctionsGetResponseBody[],
  filterHookEnabled = false,
): BusinessFunction[] => {
  if (functionGroup && functionGroup.permissions) {
    const { permissions } = functionGroup;
    const businessFunctionById: { [key: string]: FunctionsGetResponseBody } = allBusinessFunctions.reduce(
      (memo, item) => ({
        ...memo,
        [item.functionId]: item,
      }),
      {},
    );

    if (filterHookEnabled) {
      return permissions
        .filter((permission: Permission) => {
          if (permission.functionId === '1004') permission.functionId = '1045'; // TODO fix me in mocks
          const businessFunction = businessFunctionById[permission.functionId];

          return Boolean(businessFunction);
        })
        .map((permission: Permission) => permissionMapper(permission, businessFunctionById));
    }

    return permissions.map((permission: Permission) => permissionMapper(permission, businessFunctionById));
  }

  return [];
};

function permissionMapper(permission: Permission, businessFunctionById: { [key: string]: FunctionsGetResponseBody }) {
  if (permission.functionId === '1004') permission.functionId = '1045'; // TODO fix me in mocks

  const businessFunction = businessFunctionById[permission.functionId];
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const { privileges, ...rest } = businessFunction;
  const privilegesObj = PRIVILEGES.map(priv => ({
    [priv]: getBusinessFunctionPrivilege(permission, businessFunction, priv),
  })).reduce((memo, item) => ({ ...memo, ...item }));

  return {
    ...rest,
    privileges: privilegesObj,
  };
}
