import { Subject, Observable, of } from 'rxjs';
import { HostListener, Directive } from '@angular/core';
import { WithDiscardChangesComponent } from '../types';

@Directive() /* eslint-disable-next-line @angular-eslint/directive-class-suffix */
export abstract class DiscardChangesBaseComponent implements WithDiscardChangesComponent {
  isDiscardChangesModalOpen = false;

  readonly discardSubject: Subject<boolean> = new Subject<boolean>();

  protected abstract shouldPreventNavigation(): boolean;

  resolveDiscardChanges(value: boolean) {
    this.isDiscardChangesModalOpen = false;
    this.discardSubject.next(value);
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if (this.shouldPreventNavigation() && !this.isDiscardChangesModalOpen) {
      $event.returnValue = true;
    }
  }

  openDiscardChangesModal(): Observable<boolean> {
    if (this.shouldPreventNavigation()) {
      this.isDiscardChangesModalOpen = true;
      return this.discardSubject;
    }
    return of(true);
  }
}
