import { Injectable } from '@angular/core';
import { UsersHttpService, UserPermissionsSummaryGetResponseBody } from '@backbase/data-ang/accesscontrol';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

@Injectable()
export class UserPermissionsService {
  /**
   * Current user permissions
   */
  readonly userPermissions$: Observable<UserPermissionsSummaryGetResponseBody[] | null> =
    this.getUserPermissions().pipe(shareReplay(1, 1000));

  /**
   * Current user Limits permissions
   */
  readonly limitsPermissions$: Observable<any> = this.getPermissions('Limits', 'Manage Limits');

  /**
   * Payee group permissions
   */
  readonly payeeGroupPermissions$: Observable<any> = this.getPermissions('Entitlements', 'Manage Data Groups');

  /**
   * Current user Shadow Limits permissions
   */
  readonly shadowLimitsPermissions$: Observable<any> = this.getPermissions('Limits', 'Manage Shadow Limits');

  /**
   * Wizard permissions
   */
  readonly entitlementsWizardPermissions$: Observable<{ [key: string]: boolean }> = this.getPermissions(
    'Entitlements',
    'Quick User Setup',
  );

  constructor(private readonly http: UsersHttpService) {}

  /**
   * Return permissions based on provided resource and function names
   * @param resource: string - resource name
   * @param functionName: string - function name
   */
  private getPermissions(resource: string, functionName: string): Observable<any> {
    return this.userPermissions$.pipe(
      // @ts-ignore
      map((data: UserPermissionsSummaryGetResponseBody[]) => {
        // NOTE: Search key hardcoded by the purpose
        const limitsPerm = data.find(perm => perm.resource === resource && perm.function === functionName);

        return limitsPerm || undefined;
      }),
      map((perm: any) => (perm && perm.permissions) || {}),
      shareReplay(1),
    );
  }

  /**
   * Return PermissionsSummary request stream
   */
  private getUserPermissions(): Observable<UserPermissionsSummaryGetResponseBody[]> {
    return this.http.getUserPermissionsSummary();
  }
}
