import { HttpResponse } from '@angular/common/http';
import { PaginatedItemsWithPaginationParams, PaginationType } from '../model/types';

export function scanAndMergePaginatedItems<T>() {
  return (
    memo: PaginatedItemsWithPaginationParams<T>,
    item: PaginatedItemsWithPaginationParams<T>,
  ): PaginatedItemsWithPaginationParams<T> => {
    if (item.from === 0 || item.paginationType === 'pagination') {
      return item;
    }
    return {
      ...item,
      items: [...memo.items, ...item.items],
    };
  };
}

export const extractDataFromResponse = <T>(
  from: number,
  paginationType: PaginationType,
): ((value: HttpResponse<T[]>) => PaginatedItemsWithPaginationParams<T>) => {
  return (response: HttpResponse<T[]>) => ({
    items: response.body || [],
    totalItems: parseInt(response.headers.get('x-total-count') || `${(response.body || []).length}`, 10),
    from,
    paginationType,
  });
};
