import { ServiceAgreementParticipant, ServiceAgreementWithParticipants } from '../model/types';

export const extractParticipantsThatShareAccounts = (serviceAgreement: ServiceAgreementWithParticipants): string[] =>
  serviceAgreement.participants
    .filter((participant: ServiceAgreementParticipant) => participant.sharingAccounts)
    .map((participant: ServiceAgreementParticipant) => participant.id);
