import {
  ChangeDetectorRef,
  Component,
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
} from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup } from '@angular/forms';

import { Subject } from 'rxjs';
import { debounceTime, take, takeUntil } from 'rxjs/operators';

import { BbTemplate } from '@backbase/foundation-ang/core';

import { LimitBound } from '../models/limits-bound-types.model';
import { AMOUNT_DELIMETER_REG_EXP } from '../../constants/amount-delimeter-remover';

@Component({
  selector: 'bb-edit-limit',
  templateUrl: 'edit-limit.component.html',
})
export class EditLimitComponent implements OnDestroy {
  /**
   * Flag for show/hide regular limit column.
   */
  @Input() showRegularLimit = false;
  /**
   * Flag for show/hide shadow limit column.
   */
  @Input() showShadowLimit = false;
  /**
   * Limits bounds data.
   */
  @Input() set limitBounds(limitBounds: LimitBound[]) {
    const regular = this.fb.array([]);
    const shadow = this.fb.array([]);
    limitBounds.forEach((limitBound: LimitBound) => {
      // this transformation need to resolve HAD-1594 issue
      //
      // Need to cover both types
      // because API describe number amount, but server return string
      let regularAmount = limitBound.regular.amount ?? '';
      let shadowAmount = limitBound.shadow.amount ?? '';
      if (typeof regularAmount === 'string') {
        regularAmount = regularAmount.split('.')[0];
      } else {
        regularAmount = Math.trunc(regularAmount);
      }
      if (typeof shadowAmount === 'string') {
        shadowAmount = shadowAmount.split('.')[0];
      } else {
        shadowAmount = Math.trunc(shadowAmount);
      }

      regular.push(
        new FormControl(
          {
            amount: regularAmount,
            currency: ' ',
          },
          {
            updateOn: 'change',
          },
        ),
      );
      shadow.push(
        new FormControl(
          {
            amount: shadowAmount,
            currency: ' ',
          },
          {
            updateOn: 'change',
          },
        ),
      );
    });

    this.limitForm.addControl('regular', regular);
    this.limitForm.addControl('shadow', shadow);

    this.limitBoundList = limitBounds;
    this.onChanges();
  }
  /**
   * Limit currency value
   */
  @Input() currency?: string;
  /**
   * Flag to show/hide loading state for submit btn
   */
  @Input() showLoadingSubmitBtn?: string;
  /**
   * List of BE error
   */
  @Input() errors?: any[];
  /**
   * Flag for displaying view template for regular limits
   */
  @Input() disableRegular?: boolean;
  /**
   * Flag for displaying view template for shadow limits
   */
  @Input() disableShadow?: boolean;
  /**
   * Propagate event when submit button was clicked.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() submit: EventEmitter<any> = new EventEmitter<any>();
  /**
   * Close modal window event.
   * Propagate event when modal window should be closed.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() close: EventEmitter<any> = new EventEmitter<void>();

  /**
   * Reactive form for edit limits
   */
  readonly limitForm: FormGroup = this.fb.group({});
  /**
   * Bound list with full information
   */
  limitBoundList: LimitBound[] = [];
  /**
   * Flag which show when regular limits was edited
   */
  isRegularChanged = false;
  /**
   * Flag which show when shadow limits was edited
   */
  isShadowChanged = false;
  /**
   * Flag which show when form has zero value
   */
  showZeroLimitAlert = false;
  /**
   * Flag which show when user dismiss Zero info block.
   */
  zeroLimitAlertDismissed = false;

  private readonly destroy$ = new Subject();

  constructor(private fb: FormBuilder, private readonly hostEl: ElementRef, private readonly _ref: ChangeDetectorRef) {}

  /**
   * Disabled submit button getter
   */
  get isDisabled(): boolean {
    return Boolean(
      this.showLoadingSubmitBtn || this.limitForm.invalid || !(this.isRegularChanged || this.isShadowChanged),
    );
  }

  /**
   * Submit limit form
   */
  submitForm(): void {
    if (!this.validateForm()) {
      this.submit.emit({
        isRegularChanged: this.isRegularChanged,
        isShadowChanged: this.isShadowChanged,
        limitValue: this.limitForm.value,
      });
    }
  }

  /**
   * Track when form is changed
   */
  onChanges(): void {
    (this.limitForm.get('regular') as FormGroup).valueChanges.pipe(take(1), takeUntil(this.destroy$)).subscribe(() => {
      this.isRegularChanged = true;
    });

    (this.limitForm.get('shadow') as FormGroup).valueChanges.pipe(take(1), takeUntil(this.destroy$)).subscribe(() => {
      this.isShadowChanged = true;
    });

    // FE form validation
    // made in component, because custom validator/asyncValidator doesn't work properly
    this.limitForm.valueChanges.pipe(debounceTime(500), takeUntil(this.destroy$)).subscribe(() => this.validateForm());
  }

  /**
   * Function for form validation
   */
  validateForm(): boolean {
    const formGroupValue: { regular: any[]; shadow: any[] } = this.limitForm.value;
    let hasRegularError = false;
    let hasShadowError = false;

    if (formGroupValue) {
      this.limitForm.setErrors(null);

      if (this.showRegularLimit && !this.disableRegular) {
        const controlsRegular = (<FormArray>this.limitForm.get('regular')).controls;
        hasRegularError = this.checkBounds(formGroupValue.regular, controlsRegular);
      }
      if (this.showShadowLimit && !this.disableShadow) {
        const controlsShadow = (<FormArray>this.limitForm.get('shadow')).controls;
        hasShadowError = this.checkBounds(formGroupValue.shadow, controlsShadow);
      }

      // TODO check if validation working without it after updating to Angular 9
      this._ref.markForCheck();
    }

    return hasRegularError || hasShadowError;
  }

  /**
   * Function for validation one type of the limit
   * @param boundList
   * @param controls
   */
  checkBounds(boundList: any, controls: any): boolean {
    // clear all previous errors
    controls.forEach((item: any) => {
      item.setErrors(null);
    });

    for (let i = 0; i < boundList.length; i++) {
      for (let j = i + 1; j < boundList.length; j++) {
        const firstAmount =
          boundList[i]?.amount && parseInt(boundList[i].amount.toString().replace(AMOUNT_DELIMETER_REG_EXP, ''), 10);
        const secondAmount =
          boundList[j]?.amount && parseInt(boundList[j].amount.toString().replace(AMOUNT_DELIMETER_REG_EXP, ''), 10);
        if (firstAmount && secondAmount && firstAmount > secondAmount) {
          controls[i].setErrors({ biggerThan: this.limitBoundList[j].period });
          return true;
        }
      }
    }

    return false;
  }

  /**
   * Track when control has zero amount and Zero info message should be shown
   */
  shouldZeroAlertBeShown(): boolean {
    if (this.zeroLimitAlertDismissed) {
      return false;
    }
    if (this.showZeroLimitAlert) {
      return true;
    }
    const value = this.limitForm.value;
    const isZero =
      value.regular.find((item: any) => item?.amount === '0') || value.shadow.find((item: any) => item?.amount === '0');
    if (isZero) {
      this.showZeroLimitAlert = Boolean(isZero);
      this.scrollToZeroInfo();
    }
    return isZero;
  }

  /**
   * Scroll to zero limit info block
   */
  scrollToZeroInfo(): void {
    const zeroInfoElement = <HTMLElement>this.hostEl.nativeElement.querySelector('#zeroLimitInfo');
    if (zeroInfoElement) {
      setTimeout(() => {
        zeroInfoElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
      });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }
}

@Directive({
  selector: '[bbEditLimitCustomizable]',
})
export class EditLimitCustomizableDirective extends BbTemplate<EditLimitComponent, void> {}
